import React,{Component} from 'react';
import RichEditor from '../components/RichEditor'

class RichEditorPage extends Component {
    constructor(props) {
        super(props);

    }
    render() {
        return (
            <RichEditor/>
        )
    }
}

export default RichEditorPage



